*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  font: normal 16px Roboto, Verdana;
}

.gray_bg{
  background: #EEE;
}

.text_center{
  text-align: center;
}

.section{
  position: relative;
  padding: 50px 20px;
}

.section_white_triangle{
  padding-top: 60px;
}

.section_white_triangle::before{
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -40px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 40px 37.5px 0 37.5px;
  border-color: #FFF transparent transparent transparent;
}

.section_container{
  margin: 0 auto;
  max-width: 390px;
}

.section_title{
  font-size: 32px;
  font-weight: normal;
  font-family: "Roboto Slab";
